@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova:ital,wght@0,500;0,600;0,700;1,400;1,500;1,600;1,700&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
* {
  margin: 0;
  padding: 0;
}
ul {
  background-color: aquamarine;
  display: flex;
  list-style: none;
  height: 60px;
}
li {
  margin-left: 20px;
  text-decoration: none;
  margin-top: 20px;
}
/* unvisited link */
a {
  text-decoration: none;
  font-family: "Roboto", sans-serif;
}
a:visited {
  color: green;
}
a:hover {
  background-color: yellowgreen;
}

/* selected link */
a:active {
  color: yellow;
}

:root {
  --offset: 6px;
}
.Home {
  display: flex;
  justify-content: center;
}
.home-text {
  margin-top: 80px;
}
h2 {
  font-size: 5vw;
  font-family: Helvetica;
  line-height: 1.06;
  letter-spacing: -0.02em;
  color: #1d1d1f;
}

.mask {
  position: relative;
}

.mask span {
  display: block;
  box-sizing: border-box;
  position: absolute;
  padding-bottom: 6px;
  padding-bottom: var(--offset);
}
.mask span {
  color: orangered;
}

.home-container {
  margin-top: 80px;
}

.home input {
  height: 50px;
  width: 50vw;
  padding-left: 8px;
}
.home-container {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}
.home button {
  height: 50px;
  padding: 5px 10px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  border-radius: 10px;
  display: block;
  border: 0px;
  font-weight: 700;
  box-shadow: 0px 0px 14px -7px #f09819;
  background-image: linear-gradient(
    45deg,
    #ff512f 0%,
    #f09819 51%,
    #ff512f 100%
  );
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.home button:hover {
  background-position: right center;
  color: #fff;
  text-decoration: none;
}

.home button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.home button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.no-param,
.output {
  margin-top: 25px;
}

.url-input-container > input.error {
  border: 1px solid red;
}

.error-text {
  color: red;
}

.clipboard {
  display: flex;
}

.clipboard textarea {
  margin-right: 10px;
  resize: none;
  width: 200px;
  height: 40px;
}

.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 70px;
}
.contact h1 {
  font-family: Tahoma, sans-serif;
  color: orangered;
  font-size: 140%;
}
.contact form {
  display: flex;
  flex-direction: column;
}

label {
  margin-top: 8px;
  margin-bottom: 8px;
  font-family: Tahoma, sans-serif;
  font-size: 12px;
}
form input {
  width: 52vw;
  height: 30px;
  padding-left: 10px;
  font-family: Georgia, serif;
}
form textarea {
  width: 52vw;
  height: 100px;
  margin-bottom: 10px;
  padding-left: 10px;
  padding-top: 10px;
  resize: none;
  font-family: Georgia, serif;
}
form button {
  width: 54vw;
  color: black;
  border: 2px solid rgb(216, 2, 134);
  border-radius: 0px;
  padding: 18px 36px;
  display: inline-block;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 14px;
  letter-spacing: 1px;
  cursor: pointer;
  box-shadow: inset 0 0 0 0 #d80286;
  transition: ease-out 0.4s;
}
button:hover {
  color: white;
  box-shadow: inset 200vw 0 0 0 #d80286;
}


